import React from 'react';
import { graphql } from 'gatsby';
import { mapEdgesToNodes } from '../lib/helpers';
import GraphQLErrorList from '../common/components/graphql-error-list';
import SEO from '../common/components/seo';
import Hero from '../common/components/hero/Hero';
import { LandingPage } from '../domain/landingPage/forces/types';
import BlockContent from '../common/components/blockContent/BlockContent';
import PrivacyPolicy from '../common/components/privacyPolicy/PrivacyPolicy';
import Layout from '../common/components/layout/Layout';

export const query = graphql`
  query IndexPageQuery {
    site: sanitySiteSettings {
      title
      description
      keywords
    }

    landingPage: allSanityLandingPage {
      edges {
        node {
          video {
            asset {
              originalFilename
              mimeType
              url
            }
          }
          sections: _rawSections(resolveReferences: { maxDepth: 5 })
        }
      }
    }
  }
`;
interface Props {
  data: any;
  errors: any;
}

const IndexPage = (props: Props) => {
  const { data, errors } = props;

  if (errors) {
    return (
      <Layout>
        <GraphQLErrorList errors={errors} />
      </Layout>
    );
  }

  const site = (data || {}).site;
  const landingPage = mapEdgesToNodes<LandingPage>(data.landingPage)[0];

  if (!site) {
    throw new Error(
      'Missing "Site settings". Open the studio at http://localhost:3333 and add some content to "Site settings" and restart the development server.'
    );
  }
  return (
    <Layout>
      <SEO title={site.title} description={site.description} keywords={site.keywords} />
      <Hero videoUrl={landingPage.video.asset.url} />
      {landingPage.sections.map(section => (
        <BlockContent blocks={section} key={section._key} />
      ))}
    </Layout>
  );
};

export default IndexPage;
